@import "../_brand-variables.less";
@import "../_brand-mixins.less";

.fn-@{cms-ns}-pic-complex{
	overflow: hidden;
    position: relative;
    background: @body-background;

    /* &:before{
          display: block;
              content: '';
              padding-top: 48%;
      } 
      
      .complex-wrap{
          .absolute(0,_,_,0);
          .size(100%);   
      } */ 

    .item-wrap{
        float: left;
        width: 60%;

        .item{
    		border-right: 1px solid #e5e5e5;
    		border-bottom: 1px solid #e5e5e5;
            padding: 10px;
            display: block;
    		overflow: hidden;
            position: relative;
			
			.pic-left{
                width: 60%;
                float: left;
            }

            .img-box{
                width: 40%;
                float: left;

                &:before{

                    padding-top: 100%;
                }
            }

            &:last-child{
				border-bottom: 0;	
			}
    	}
    }

    .item-big{
        .size(40%,100%);
        padding: 10px;
        float: left;
        display: block;
        position: relative;

        .img-box{
            display:block;
            margin-top: 10px;
            width: 100%;
        }
    }

    .title{
        font-size: @font-size-lg;
        color: @text-color;
        .ellipsis();
    }

    .min-tit{
        color: #fa3204;
        font-size: @font-size-sm;
        .ellipsis();
    }
}

@media(max-width:@screen-xs){
    .fn-@{cms-ns}-pic-complex{
        .item-big{
            .img-box{
                margin-top: 8px;
            }   
        }  
    }
}
@media(min-width:@screen-md-max ){
    .fn-@{cms-ns}-pic-complex{
        .item-big{
            .img-box{
                margin-top: 40px;
            }   
        }  
    }
}