@import "../../_plugin/cmp-item-list.less";

@import "../_brand-variables.less";
@import "../_brand-mixins.less";

.fn-@{cms-ns}-prolist{
	.item-v-list{
		.cmp-item-list();

		.item{

			.shopping-trolley{
				.size(30px);
				.absolute(_,0,4px,_);
				background: #ffefef;
				border: 1px solid #e37690;
				border-radius: 30px;
				margin-right: 5%;
				text-align: center;

				i{
					color: @ht-color;
					font-size: unit(@font-size-lg, rem);
					padding: 7px 0 0;
				}
			}
		}
	}
}
