@import "../_brand-variables.less";
@import "../_brand-mixins.less";


.fn-@{cms-ns}-pic-floor{
	overflow: hidden;
    position: relative;
    background: @body-background;

    /* &:before{
    	display: block;
    	    content: '';
    	    padding-top: 58.7%;
    } */

    .floor1{
    	overflow: hidden;
    	.item{
    		width: 50%;
    		border-right: 1px solid #e5e5e5;
    		padding: 10px;
            float: left;
            display: block;

    		> *{
            	width: 50%;
                float: left;	
            }

            &:last-child{
            	border-right: 0;	
            }
			
			.img-box{

                &:before{

                    padding-top: 100%;
                }
            }
         }
    }

    .floor2{
    	overflow: hidden;
    	.item-big{
	    	width: 33.33%;
	    	float: left;
	    	padding: 10px;
	    	border-right: 1px solid #e5e5e5;
	    	border-top: 1px solid #e5e5e5;
	    	position: relative;

	    	&:last-child{
				border-right: 0;
	    	}

	    	/* .pic-left{
	    		.absolute(10px,_,_,10px);
	    	} */

	    	.img-box{
	            display:block;
	            margin-top: 10px;
	            width: 70%;
	            float: right;
	        }
	    }
    }

    .pic-left{
		.title{
            font-size: @font-size-lg;
            color: @text-color;
            .ellipsis();
        }

        .min-tit{
            color: #8b55fb;
            font-size: @font-size-sm;
            .ellipsis();
        }
	}

    
}