@import "../_brand-variables.less";
@import "../_brand-mixins.less";

@import "../../_plugin/cmp-level-flex.less";

.fn-@{cms-ns}-level-prolist01{
	background: @body-background;

	.item-list{
		
		.cmp-level-list();

		.title{
			.ellipsis();
			display: block;
		}

		.min-title{
			height: unit(@line-height-computed-sm * 3, rem);
			line-height: 1.4;
			color: #999;
			font-size: @font-size-sm;
			padding-top: 5px;
			overflow: hidden;
		}

		.item{
			padding: 0 0 10px;

		}
	}
}


@media(max-width:@screen-xs){
	.fn-@{cms-ns}-level-prolist01{
		.item-list{
			.min-title{
				height: unit(@line-height-computed-base * 2, rem);
				line-height: 1.5;
			}	
		}
	}
}