@import "common/cmp/cmp-swiper.less";

@import "../_brand-variables.less";
@import "../_brand-mixins.less";

.fn-@{cms-ns}-brand-slide{
	.cmp-swiper();

	.swiper-container{
		.swiper-pagination{
			text-align: center;
		}	
	}

	.swiper-pagination-bullet{
		@active-color:#d5d5d5;

		border-color:@active-color !important;
		background: @active-color;

		&-active{
			background: @ht-color;
			border: .02rem solid @ht-color !important;
		}
	}
	.logo-wrap{
		padding: 3% 3% 2% 0;
		background: #fff;
		margin: 0 auto;
		overflow: hidden;

		.logo-list{
			width: 22%;
			float: left;
			display: block;
			margin:0 0 3% 3%;

			&:before{
				padding-top: 50%;
			}
		}
	}
}
