@import "../_brand-variables.less";
@import "../_brand-mixins.less";

@import "../../_plugin/cmp-common-title.less";

.fn-@{cms-ns}-common-header{
	.cmp-common-title(@brand-color,@brand-color);

	position:relative;
	background: @body-background;
	border-bottom: 1px solid @container-bg-color;

	.line-wid{
		&:before,&:after{
			.size(30%,1px);
			
		}
		&:before{
			left:-30%;
		}
		&:after{
			right:-30%;
		}
	}

	.title01{
		color: #f92e65;
		&:before,&:after{
			background: #f92e65;
		}
	}

	.title02{
		color: #8b55fb;
		&:before,&:after{
			background: #8b55fb;
		}
	}

	.title03{
		color: #3db8fa;
		&:before,&:after{
			background: #3db8fa;
		}
	}

	.title04{
		color: #f7af0d;
		&:before,&:after{
			background: #f7af0d;
		}
	}

	.title05{
		color: #fe2d9a;
		&:before,&:after{
			background: #fe2d9a;
		}
	}

	.more{
		.absolute(4px,3.2%,_,_);
		font-size: @font-size-sm;
		color: #666;
	}
}