@import "../_brand-variables.less";
@import "../_brand-mixins.less";

@import "../../_plugin/cmp-level-flex.less";

.fn-@{cms-ns}-level-prolist{
	background: @body-background;

	.item-list{
		
		.cmp-level-list();

		.item{
			padding: 0 0 10px;

			.shopping-trolley{
				.size(30px);
				.absolute(_,0,10px,_);
				background: #ffefef;
				border: 1px solid #e37690;
				border-radius: 30px;
				margin-right: 5%;
				text-align: center;

				i{
					color: @ht-color;
					font-size: unit(@font-size-lg, rem);
					padding: 7px 0 0;
				}
			}
		}
	}
}