@import "../_brand-variables.less";
@import "../_brand-mixins.less";


.fn-@{cms-ns}-pic-two{
	overflow: hidden;
    position: relative;
    background: @body-background;

    .item-big{
    	width: 50%;
    	float: left;
    	padding: 10px;
    	border-right: 1px solid #e5e5e5;

    	&:last-child{
			border-right: 0;
    	}

    	.pic-left{
    		.title{
	            font-size: @font-size-lg;
	            color: @text-color;
	            .ellipsis();
	        }

	        .min-tit{
	            color: #fa3204;
	            font-size: @font-size-sm;
	            .ellipsis();
	        }
    	}

    	.img-box{
            display:block;
            margin-top: 10px;
            width: 100%;
        }
    }
}